@import '../../../base';

$padingTop: 130px;
$padingBottom: 30px;
$textMaxWidth: 332px;
$carouselMaxWidth: 450px;

.rewards-container {
  .welcome {
    padding-top: $padingTop;
    padding-bottom: $padingBottom;

    @media screen and (max-width: 767px) {
      padding-top: ($padingTop - 80px);
    }

    // welcome__title
    &__title {
      max-width: $textMaxWidth;

      @media screen and (max-width: 767px) {
        max-width: unset;
      }
    }

    // welcome_sub-title
    &__sub-title {
      max-width: $textMaxWidth;
      font-size: 14px;
      margin-bottom: 25px;
      color: $color-input-form-landing-desc2;

      @media screen and (max-width: 767px) {
        max-width: unset;
      }
    }

    // welcome__main-img
    &__main-img {
      max-width: 100%;
    }

    // welcome__partner-title
    &__partner-title {
      font-size: 12px;
      margin-bottom: 10px;
      color: $color-input-title-inactive;
    }

    .slick-carousel {
      max-width: $carouselMaxWidth;

      @media screen and (max-width: 767px) {
        max-width: unset;
      }

      .slick-slide {
        padding-left: 5px !important;
        padding-right: 5px !important;

        img {
          max-width: 100%;
          width: ($carouselMaxWidth - 60px) / 4 - 10px;
        }
      }
    }
  }
}