@import '../../../base';

$defaultFontSize: 14px;
$defaultFontWeight: 600;
$defaultColor: $color-input-form-landing-desc2;

$indicatorWidth: 216px;
$indicatorBgActive: $color-input-light-blue;
$indicatorBgHover: rgba($color-input-light-blue, 0.3);
$indicatorFontSize: 18px;
$indicatorFontWeight: bold;
$indicatorColor: $color-input-text;
$indicatorBorderRadius: 50px;

.rewards-container {
  .guides {
    width: 100%;
    margin: 0 auto;
    font-size: $defaultFontSize;
    font-weight: $defaultFontWeight;
    color: $defaultColor;

    .gift-box-image {
      display: block;
      max-width: 100%;
      width: 250px;
      margin: 0 auto 30px auto;

      @media screen and (max-width: 767px) {
        display: none;
      }
    }

    .indicators {
      display: block;
      position: relative;
      margin: 0 auto;

      @media screen and (max-width: 767px) {
        display: flex;
        flex-direction: row;
        margin: 0 auto 40px auto;
      }

      @media screen and (max-width: 375px) {
        flex-direction: column;
      }
    }

    .indicator {
      outline: none;
      display: block;
      max-width: 100%;
      margin: 0 auto 40px auto;
      padding: 10px 24px;
      border-radius: $indicatorBorderRadius;
      width: $indicatorWidth;
      background-color: transparent;
      font-size: $indicatorFontSize;
      font-weight: $indicatorFontWeight;
      color: $indicatorColor;
      transition: all 0.3s ease-in-out;

      @media screen and (max-width: 767px) {
        display: inline-block;
        width: 50%;
        margin: unset;
        background-color: rgba($indicatorBgHover, 0.2);
      }

      @media screen and (max-width: 425px) {
        font-size: 14px;
        // display: inline-block;
      }

      @media screen and (max-width: 375px) {
        width: unset;
        padding: 18px 24px;
        margin-bottom: 1rem;
      }

      &:hover {
        background-color: $indicatorBgHover;
      }

      &:focus {
        outline: none;
      }

      &.active {
        background-color: $indicatorBgActive;
      }
    }

    .carousel-item {
      display: block;
      opacity: 0;
      transition: visibility 0.6s ease-in-out, opacity 0.6s ease-in-out;
      transform: unset;
      visibility: collapse;

      &.active {
        opacity: 1;
        visibility: visible;
      }
    }

    .offset-insurance-policies {
      p {
        padding-right: 50px;

        @media screen and (max-width: 767px) {
          padding-right: unset;
        }
      }
    }

    .rewards-marketplace {
      .item {
        display: block;
        width: 100%;
      }

      .image {
        width: 100%;
        background-size: contain;
        background-repeat: no-repeat;
        background-position: left center;

        @media screen and (max-width: 575px) {
          display: none;
        }
      }

      .title {
        font-weight: bold;
        font-size: 18px;
        text-align: center;
        margin: 1rem 0;
        padding-right: 31.87%;
        color: $color-input-text;

        @media screen and (max-width: 575px) {
          padding-right: unset;
        }
      }

      .desc {
        text-align: left;
        padding-right: 31.87%;

        @media screen and (max-width: 575px) {
          padding-right: unset;
        }
      }
    }
  }
}