@import '../../base';

.rewards-container {
  .form-landing-information-container {
    .main-title {
      font-size: 7.313rem;

      @media screen and (max-width: 1090px) {
        font-size: 7rem;
      }
    }
  }

  .credits {
    width: 100%;
    margin: 0 auto;

    .item {
      padding: 0 0.5rem;

      .image {
        display: flex;
        margin-left: auto;
        margin-right: auto;
        box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.05);
        background-color: #fff;
        border-radius: 50%;
        width: 160px;
        height: 160px;
        padding: 40px;
        text-align: center;
        justify-content: center;

        @media screen and (max-width: 992px) {
          width: 120px;
          height: 120px;
          padding: 35px;
        }

        > img {
          max-width: 100%;
          max-height: 100%;
        }
      }

      .title {
        color: $color-input-text;
        font-size: 18px;
        margin: 20px auto;
        text-align: center;
        font-weight: bold;
      }

      .desc {
        color: $color-input-form-landing-desc2;
        font-size: 14px;
        font-weight: 600;
        max-width: 200px;
        text-align: center;
        margin: 0 auto;
      }
    }
  }
  
  .blogs {
    width: 100%;
    margin: 0 auto;

    .item {
      padding: 0 0.5rem;

      .image {
        display: block;
        margin-left: auto;
        margin-right: auto;
        box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.05);
        background-repeat: no-repeat;
        background-position: center center;
        background-size: cover;
        background-color: #fff;
        border-radius: 50%;
        max-width: 100%;
        width: 100%;
        padding-top: 60%;
        border-radius: 10px;
        cursor: pointer;
      }

      .title {
        color: $color-input-text;
        font-size: 18px;
        margin-top: 11px;
        margin-bottom: 6px;
        text-align: left;
        font-weight: bold;
        cursor: pointer;
      }

      .desc {
        color: $color-input-form-landing-desc2;
        font-size: 14px;
        font-weight: 600;
        text-align: left;
      }
    }
    
  }

  .where-to-check {
    width: 100%;
    margin: 0 auto;

    .image {
      max-width: 100%;
      margin-left: auto;
      margin-right: auto;

      @media screen and (max-width: 767px) {
        margin-bottom: 1.25rem;
      }
    }

    .title {
      color: $color-input-text;
      font-size: 18px;
      font-weight: bold;
      margin-bottom: 6px;
    }
    
    .desc {
      color: $color-input-form-landing-desc2;
      font-size: 14px;
      font-weight: 600;
      margin-bottom: 50px;
      
    }

    .info {
      display: block;
      max-width: 300px;
      margin-right: 0;
      margin-left: auto;

      @media screen and (max-width: 767px) {
        max-width: unset;
        margin-right: unset;
        margin-left: unset;
      }
    }
  }

  .tnc {
    .card-body > ol {
      color: $color-input-text;
      font-weight: bold;
    }

    .image {
      max-width: 100%;
      width: 700px;
    }

    .term-block {
      margin-bottom: 1rem;

      .title {
        font-size: 16px;
        font-weight: bold;
        color: $color-input-text;
      }

      .details-list {
        list-style: square;
        color: $color-input-form-landing-desc2;
        font-size: 14px;
        font-weight: 600;
      }
    }
  }
}